import axios from "@/api/axios";

// const getJudPlayers = () => {
//   return axios.get("/upload/sport");
// };
const getCompetition = () => {
  return axios.get("/players/competition");
};
const postIDCompetition = (id) => {
  return axios.get(`/players/competition/${id}`);
};
const setResult = (score) => {
  return axios.post(`/players/set_result`, score);
};
const getAllResults = (credentials) => {
  return axios.get(
    `/players/get/all/result/${credentials.nomination}/${credentials.brigade}`
  );
};
const getAllResultsAdmin = (credentials) => {
  return axios.get(`/admin/result/all/${credentials.nomination}`);
};
const getNomination = (credentials) => {
  return axios.get(
    `/players/nomination/${credentials.competition}/${credentials.brigade}`
  );
};
const getNominationAdmin = (credentials) => {
  return axios.get(`/admin/all/nomination/${credentials.competition}`);
};
const getAllUsers = () => {
  return axios.get(`/players/get/all/user`);
};
const getProtocol = (info) => {
  return axios.get(`/protocol?age=${info.age}&nomination=${info.nomination}`);
};
const postAllResult = (result) => {
  return axios.post(`/admin/result/add`, result);
};
const updateResult = (result) => {
  return axios.post(`/admin/edit/score`, result);
};
const postTelevision = (result) => {
  return axios.post(`/protocol/tv`, result);
};
const openScoreArbitr = (id) => {
  return axios.post(`/admin/open/rating/${id}`);
};
export default {
  getCompetition,
  postIDCompetition,
  setResult,
  getAllResults,
  getNomination,
  getAllUsers,
  getProtocol,
  getNominationAdmin,
  getAllResultsAdmin,
  postAllResult,
  updateResult,
  postTelevision,
  openScoreArbitr,
};
