<template>
  <td>
    <input
      type="text"
      id="item-deduction"
      v-if="edit"
      :value="valueLocal"
      @keyup.enter="
        valueLocal = $event.target.value;
        edit = false;
        $emit('input', { score: Number(valueLocal), judge: numberJuj });
      "
      v-focus=""
      style="width: 30px; height: 29px"
    />
    <!-- $emit('input', { score: Number(valueLocal), judge: numberJuj }); -->
    <p
      v-else
      @click="edit = true"
      style="
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
      "
    >
      {{ valueLocal }}
    </p>
  </td>
  <!-- @blur="
        valueLocal = $event.target.value;
        edit = false;
        $emit('input', valueLocal);
      " -->
</template>

<script>
export default {
  props: ["value", "numberJuj"],

  data() {
    return {
      edit: false,
      valueLocal: this.value,
      juj: this.numberJuj,
      // newScore: [],
    };
  },

  watch: {
    value: function () {
      this.valueLocal = this.value;
      this.juj = this.numberJuj;
    },
  },
  methods: {
    onChange() {
      let newScore = {
        score: Number(this.valueLocal),
        juj: this.juj,
      };
      // this.newScore.push({ score: Number(this.valueLocal), judge: this.juj });
      this.$emit("input", newScore);
    },
  },

  directives: {
    focus: {
      inserted(el) {
        el.focus();
      },
    },
  },
};
</script>
