<template>
  <div class="arbitr">
    <div class="arbitr__select-wrap">
      <div class="select-block__item-block">
        <label for="standard-select"> Выберите номинацию:</label>
        <div class="select">
          <select
            required
            id="standard-select"
            v-model="choiceNomination"
            @change="onChangeSelected"
          >
            <option
              v-for="nomination in this.nominationAdmin"
              :key="nomination.id"
              :value="nomination"
            >
              {{ nomination.name }} {{ nomination.age }}
            </option>
          </select>
          <span class="focus"></span>
        </div>
      </div>
      <div class="" style="display: flex; margin: 0px 0px 0px 30px">
        <div class="" style="margin: 0px 40px 0px 40px">
          <div class="select-block__item-block">
            <label for="standard-select"> Выберите шапку:</label>
            <div class="select">
              <select required id="standard-select" v-model="choiceHeader">
                <option
                  value="III этап Кубка города Новосибирска «Весенняя капель»: 22-23 апреля 2023 г."
                >
                  III этап Кубка города Новосибирска по спортивной аэробике
                  "Весенняя капель"
                </option>
                <option
                  value="46-я Универсиада студентов высших учебных заведений Новосибирской области 2022-2023 уч.г.: 23 апреля 2023 г."
                >
                  Универсиада студентов высших учебных заведений Новосибирской
                  области
                </option>
              </select>
              <span class="focus"></span>
            </div>
          </div>
        </div>
        <div class="">
          <div class="select-block__item-block">
            <label for="standard-select"> Выберите этап:</label>
            <div class="select">
              <select required id="standard-select" v-model="choiceStage">
                <!-- <option value="КВАЛИФИКАЦИЯ">Квалификация</option> -->
                <option value="ФИНАЛ">Финал</option>
              </select>
              <span class="focus"></span>
            </div>
          </div>
        </div>
      </div>
      <div class="arbitr-score">
        <input
          type="button"
          value="Скачать протокол"
          class="admin-btn"
          @click="download()"
        />
      </div>
    </div>
    <div class="arbitr__message" v-if="!this.choiceNomination">
      <p>Выберите номинацию для просмотра протоколов</p>
    </div>
    <template v-else>
      <TableArbitrator
        v-for="(nomination, index) in this.nominationAdmin"
        :key="index"
        @changeData="onChangeSelected()"
        v-bind:item_nomination="nomination"
        v-bind:choiceNomination="checkNom"
      />
    </template>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { actionTypesJudgind } from "@/store/modules/judging";
import { actionTypesSocket } from "@/store/modules/socket";
import TableArbitrator from "../components/TableArbitrator.vue";
import axios from "axios";

export default {
  name: "AppScoreAdminMain",
  components: {
    TableArbitrator,
  },
  data() {
    return {
      choiceNomination: null,
      choiceHeader:
        "III этап Кубка города Новосибирска «Весенняя капель»: 22-23 апреля 2023 г.",
      choiceStage: "ФИНАЛ",
      choiceData: {},
      checkListNomination: {},
      newRating: [],
      dataDownload: {},
      newData: {},
    };
  },
  computed: {
    ...mapState({
      infoUser: (state) => state.auth.infoUser,
      allResults: (state) => state.judging.allResults,
      nominationAdmin: (state) => state.judging.nominationAdmin,
      messageArbitr: (state) => state.socket.messageArbitr,
      nowCompetition: (state) => state.judging.nowCompetition,
    }),
    checkNom() {
      return this.choiceNomination;
    },
  },

  methods: {
    onChangeSelected() {
      this.choiceData = {
        nomination: this.choiceNomination.id,
      };
      //!ПОЛУЧЕНИЕ ТЕКУЩИХ РЕЗУЛЬТАТОВ ДЛЯ АДМИНА
      this.$store
        .dispatch(actionTypesJudgind.getAllResultsAdmin, this.choiceData)
        .then((resp) => {
          console.log(resp);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    closeSportsmenEdit(someData) {
      let objIndex = this.score.findIndex(
        (obj) => obj.name == someData.sportsmen
      );
      this.score[objIndex].name = "Laila";
      this.newData = JSON.parse(
        JSON.stringify(this.score[objIndex]),
        (key, value) =>
          key === someData.newScore.judge ? someData.newScore.score : value
      );
      this.score[objIndex] = this.newData;
    },
    download() {
      axios
        .post(
          `/protocol?age=${this.choiceNomination.age}&nomination=${this.choiceNomination.name}&choiceHeader=${this.choiceHeader}&choiceStage=${this.choiceStage}`,
          this.allResults,
          {
            responseType: "blob",
          }
        )
        .then((response) => {
          console.log(response);
          const url = URL.createObjectURL(
            new Blob([response.data], {
              type: "application/vnd.ms-excel",
            })
          );
          var fileName = `${this.choiceNomination.age}${this.choiceNomination.name}`;
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", fileName);
          document.body.appendChild(link);
          link.click();
        });
    },
    //  download() {
    //    // this.$store
    //    //   .dispatch(actionTypesJudgind.getProtocol, this.dataDownload)
    //    //   .then(() => {})
    //    //   .catch((er) => {
    //    //     console.log("Ошибка", er);
    //    //   });
    //  },
  },
  mounted() {
    this.$store.dispatch(actionTypesSocket.socketConnect, this.infoUser);
    this.checkListNomination = {
      competition: this.nowCompetition,
    };
    this.$store
      .dispatch(actionTypesJudgind.getNominationAdmin, this.checkListNomination)
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
  },
};
</script>
