<script>
import { mapState } from "vuex";
import ClickToEdit from "./ClickToEdit.vue";
import { actionTypesJudgind } from "@/store/modules/judging";
export default {
  name: "AppTableMainItem",
  data() {
    return {
      edit: false,
      newScore: [],
      doublePrices: {},
      testNewScore: [],
      putScoreSportsmen: {},
    };
  },
  props: {
    item_sportsmen: {
      type: Object,
      default() {
        return {};
      },
    },
    idx: {
      type: Number,
      default() {
        return {};
      },
    },
    choiceNomination: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  components: {
    ClickToEdit,
  },
  computed: {
    maxScoreExe() {
      return Math.max.apply(
        null,
        Object.values(this.item_sportsmen.executionAllResponse)
      );
    },
    minScoreExe() {
      return Math.min.apply(
        null,
        Object.values(this.item_sportsmen.executionAllResponse)
      );
    },
    maxScoreArt() {
      return Math.max.apply(
        null,
        Object.values(this.item_sportsmen.artistryAllResponse)
      );
    },
    minScoreArt() {
      return Math.min.apply(
        null,
        Object.values(this.item_sportsmen.artistryAllResponse)
      );
    },
    averageExe() {
      return +(
        (Object.values(this.item_sportsmen.executionAllResponse).reduce(
          (a, b) => a + b
        ) -
          this.minScoreExe -
          this.maxScoreExe) /
        2
      ).toFixed(3);
    },

    averageArt() {
      return +(
        (Object.values(this.item_sportsmen.artistryAllResponse).reduce(
          (a, b) => a + b
        ) -
          this.minScoreArt -
          this.maxScoreArt) /
        2
      ).toFixed(3);
    },
    averageDif() {
      if (
        this.choiceNomination.age === "18+" &&
        (this.choiceNomination.name === "ТР" ||
          this.choiceNomination.name === "ТР 18+" ||
          this.choiceNomination.name === "ГР" ||
          this.choiceNomination.name === "ГР 18+")
      ) {
        return +(this.item_sportsmen.difficultyAllResponse.dif1 / 1.7).toFixed(
          3
        );
      } else return this.item_sportsmen.difficultyAllResponse.dif1 / 2;
    },
    dedElem() {
      if (this.item_sportsmen.deductionAllResponse.deductionDifficulty) {
        return this.item_sportsmen.deductionAllResponse.deductionDifficulty;
      } else {
        return 0;
      }
    },
    averageDed() {
      return +(
        this.item_sportsmen.deductionAllResponse.deductionArbitrator +
        this.item_sportsmen.deductionAllResponse.deductionLine +
        this.item_sportsmen.deductionAllResponse.deductionDifficulty
      );
      // return +(
      //   Object.values(this.item_sportsmen.deductionAllResponse).reduce(
      //     (a, b) => a + b
      //   ) - this.dedElem
      // ).toFixed(2);
    },
    overallScore() {
      return +(
        this.averageExe +
        this.averageArt +
        this.averageDif -
        this.averageDed
      ).toFixed(3);
    },
    ...mapState({
      infoUser: (state) => state.auth.infoUser,
    }),
  },
  methods: {
    closeSportsmenEdit() {
      this.putScoreSportsmen = {
        sportsmen: this.item_sportsmen.id,
        newScore: this.newScore,
        idUser: this.infoUser.id,
        averageExe: this.averageExe,
        averageArt: this.averageArt,
        averageDif: this.averageDif,
        averageDed: this.averageDed,
        overallScore: this.overallScore,
      };
      console.log("closeSportsmenEdit", this.putScoreSportsmen);
      this.$store
        .dispatch(actionTypesJudgind.updateResult, this.putScoreSportsmen)
        .then((resp) => {
          console.log(resp);
          this.$emit("changeData");
          this.newScore = [];
          this.putScoreSportsmen = [];
        })
        .catch((error) => {
          console.log(error);
        });
      this.edit = !this.edit;
    },
    input(value) {
      if (
        value.judge === "exe1" ||
        value.judge === "exe2" ||
        value.judge === "exe3" ||
        value.judge === "exe4"
      ) {
        this.item_sportsmen.executionAllResponse[value.judge] = value.score;
      }
      if (
        value.judge === "art1" ||
        value.judge === "art2" ||
        value.judge === "art3" ||
        value.judge === "art4"
      ) {
        this.item_sportsmen.artistryAllResponse[value.judge] = value.score;
      }
      if (value.judge === "dif1" || value.judge === "dif2") {
        this.item_sportsmen.difficultyAllResponse[value.judge] = value.score;
      }
      if (value.judge === "deductionDifficulty") {
        this.item_sportsmen.deductionAllResponse[value.judge] = value.score;
      }

      this.newScore.push(value);
    },
    editItem() {
      this.edit = !this.edit;
    },

    putAxios() {
      console.log("Меняем");
      const forms = document.forms;
      const formData = new FormData(forms[1]);
      this.newcritical = [];
      for (let [name, value] of formData) {
        this.newcritical = { ...this.newcritical, [name]: value };
        console.log(this.newcritical);
        console.log(`${name}: ${value}`);
      }
      // this.$store
      //   .dispatch(actionTypes.putCompanieItem, {
      //     amount: this.storage_item.amount,
      //     avg_consumption: this.storage_item.avg_consumption,
      //     critical: Number(this.newcritical.critical),
      //     days_left: this.storage_item.days_left,
      //     for_order: this.storage_item.for_order,
      //     // id: this.storage_item.id,
      //     company_id: this.storage_item.company_id,
      //     product_title: this.storage_item.product_title,
      //     unit: this.storage_item.unit,
      //   })
      //   .then(() => {
      //     console.log("Put");
      //     this.$emit("onSelect");
      //     this.edit = !this.edit;
      //   })
      //   .catch((er) => {
      //     console.log("Ошибка", er);
      //   });
    },
  },
};
</script>

