<template>
  <div
    class=""
    v-bind:class="{ none: choiceNomination.id !== item_nomination.id }"
  >
    <table class="table_blur">
      <thead class="thead">
        <tr class="tr">
          <th class="th"></th>
          <th class="th"></th>
          <th class="th"></th>
          <th class="th" colspan="5">Исполнение</th>
          <th class="th" colspan="5">Артистичность</th>
          <th class="th" colspan="3">Сложность</th>

          <th class="th" colspan="4">Сбавки</th>
          <!-- <th class="th" colspan="4" v-else>Сбавки</th> -->

          <th class="th" rowspan="2">Общий балл</th>
          <th class="th" rowspan="2">Место</th>
          <th rowspan="2"></th>
        </tr>
        <tr class="tr">
          <th class="th">№</th>
          <th class="th">Участник</th>
          <th class="th">Субъект РФ</th>
          <th class="th">1</th>
          <th class="th">2</th>
          <th class="th">3</th>
          <th class="th">4</th>
          <th class="th">СР</th>
          <th class="th">1</th>
          <th class="th">2</th>
          <th class="th">3</th>
          <th class="th">4</th>
          <th class="th">СP</th>
          <th class="th">1</th>
          <th class="th">2</th>
          <th class="th">СР</th>
          <th class="th">Слож</th>
          <th class="th">Лин</th>
          <th class="th">Арбитр</th>
          <!-- <th class="th">КОД</th> -->
          <th class="th">общ</th>
        </tr>
      </thead>
      <tbody class="tbody">
        <template v-if="this.infoUser.role === 'ROLE_ARB'">
          <TableArbitratorItem
            v-for="(score, index) in this.sort"
            :key="index"
            @changeData="$emit('changeData')"
            v-bind:item_sportsmen="score"
            v-bind:choiceNomination="choiceNomination"
            v-bind:idx="index"
          />
        </template>
        <template
          v-if="
            this.infoUser.role === 'ROLE_MAIN' || this.infoUser.role === 'ADMIN'
          "
        >
          <TableMainJujItem
            v-for="(score, index) in this.mainGeneralProt"
            :key="index"
            @changeData="$emit('changeData')"
            v-bind:item_sportsmen="score"
            v-bind:choiceNomination="choiceNomination"
            v-bind:idx="index"
          />
          <!-- @closeSportsmenEdit="closeSportsmenEdit" -->
          <!-- @closeScore="closeScore" -->
        </template>
      </tbody>
    </table>
    <div class="table-btn-sort">
      <input
        type="submit"
        value="От А до Я ↕"
        class="button btn-green"
        style="padding: 1rem 2rem !important"
        @click="isSort = !isSort"
        v-if="this.infoUser.role === 'ROLE_ARB'"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import TableArbitratorItem from "../components/TableArbitratorItem.vue";
import TableMainJujItem from "../components/TableMainJujItem.vue";
export default {
  name: "AppTableArbitrator",
  data() {
    return {
      isSort: false,
    };
  },
  components: {
    TableArbitratorItem,
    TableMainJujItem,
  },
  props: {
    item_nomination: {
      type: Object,
      default() {
        return {};
      },
    },
    choiceNomination: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    ...mapState({
      infoUser: (state) => state.auth.infoUser,
      // allResultsAdmin: (state) => state.judging.allResultsAdmin,
      allResults: (state) => state.judging.allResults,
      nomination: (state) => state.judging.nomination,
      messageArbitr: (state) => state.socket.messageArbitr,
      nowCompetition: (state) => state.judging.nowCompetition,
    }),
    sort() {
      if (this.isSort && this.allResults) {
        return [...this.allResults].sort((a, b) => a.place - b.place);
        // return this.allResults.sort((a, b) => b.place - a.place);
        // return this.allResults.sort((a, b) => a.place - b.place);
      } else {
        return this.allResults;
      }
    },
    mainGeneralProt() {
      var filter;
      if (this.allResults) {
        filter = [...this.allResults].sort((a, b) => a.place - b.place);
      }
      return filter;
    },
  },
};
</script>

