<template>
  <tr class="tr" v-bind:class="classObject">
    <!-- v-bind:class="{close: this.item_sportsmen.isClose}" -->
    <!-- v-bind:class="{ active: isActive }" -->
    <td class="td">{{ idx + 1 }}</td>
    <td
      class="td"
      @click="openSportsmen"
      style="width: 20%; text-align: left; padding: 10px 0px 10px 30px"
    >
      <p
        style="font-weight: 500; color: black; font-size: 15px"
        v-for="(name, index) in item_sportsmen.fio"
        :key="index"
      >
        {{ name }}
      </p>
    </td>
    <td class="td" style="font-weight: 500; color: black; font-size: 15px">
      {{ item_sportsmen.club }}
    </td>
    <!-- <template v-if="this.edit === false"> -->
    <td
      class="td"
      style="color: black; font-size: 15px"
      v-for="(exe, index) in item_sportsmen.executionAllResponse"
      :key="index"
    >
      {{ exe }}
    </td>
    <!-- </template> -->

    <td class="td table__average-exe">{{ averageExe }}</td>
    <td
      class="td"
      style="color: black; font-size: 15px"
      v-for="(art, index) in item_sportsmen.artistryAllResponse"
      :key="index"
    >
      {{ art }}
    </td>

    <td class="td table__average-art">{{ averageArt }}</td>
    <td
      class="td"
      style="color: black; font-size: 15px"
      v-for="(dif, index) in item_sportsmen.difficultyAllResponse"
      :key="index"
    >
      {{ dif }}
    </td>
    <!-- <td v-if="item_sportsmen.difficulty.length != 1">-</td> -->
    <td class="td table__average-dif">{{ averageDif }}</td>
    <!-- Оценка сбавки за сложность -->
    <td class="td" v-if="dedElem != 0">{{ dedElem }}</td>
    <td class="td" v-else></td>
    <!-- Оценка сбавки за линию -->
    <template v-if="!this.item_sportsmen.isClose">
      <td class="td">
        <input
          type="number"
          v-model="deduction_line"
          id="item-deduction"
          name="deduction"
          step="0.1"
          max="10"
          placeholder="0.1"
        />
      </td>

      <td class="td">
        <input
          type="number"
          v-model="deduction_arb"
          id="item-deduction"
          name="deduction"
          max="10"
          step="0.5"
          placeholder="1.0"
        />
      </td>
      <!-- <td class="td">
        <CodDeduction @criterion="criterion" />
      </td> -->
    </template>
    <template v-if="this.item_sportsmen.isClose">
      <td class="td">
        {{ this.item_sportsmen.deductionAllResponse.deductionLine }}
      </td>

      <td class="td">
        {{ this.item_sportsmen.deductionAllResponse.deductionArbitrator }}
      </td>
      <!-- <td class="td">
        {{ this.item_sportsmen.deductionAllResponse.deductionArbitratorCode }}
      </td> -->
    </template>
    <td class="td" v-if="averageDed != 0">{{ averageDed }}</td>
    <td class="td" v-else></td>
    <td class="td table__general">{{ overallScore }}</td>
    <td class="td table__average-range">{{ this.item_sportsmen.place }}</td>
    <!-- v-if="role === 'ROLE_ADMIN'" -->
    <td class="td" v-bind:class="{ display: this.item_sportsmen.isClose }">
      <input
        type="submit"
        value="Закрыть"
        class="button btn-green"
        @click="submitScore()"
        v-if="!this.item_sportsmen.isClose"
      />
      <input
        type="submit"
        value="Открыть"
        class="button btn-login"
        @click="updateScore()"
        v-if="this.item_sportsmen.isClose"
      />

      <div class="television-icon" v-if="this.item_sportsmen.isClose">
        <img
          src="../assets/img/shelf.svg"
          alt=""
          class="television-icon__svg"
          @click="submitTelevision()"
        />
      </div>

      <!-- <input
        type="submit"
        value="Телевизор"
        class="button btn-green"
        @click="submitTelevision()"
        v-if="this.item_sportsmen.isClose"
      /> -->
      <!-- :disabled="!this.overallScore" -->
    </td>
  </tr>
</template>

<script>
// import ClickToEdit from "./ClickToEdit.vue";
// import CodDeduction from "./CodDeduction.vue";
import { actionTypesJudgind } from "@/store/modules/judging";
import { actionTypesSocket } from "@/store/modules/socket";
export default {
  name: "AppTableArbitratorItem",
  data() {
    return {
      edit: false,
      newScore: {},
      deduction_arb: null,
      deduction_line: null,
      code: null,
      isActive: false,
      newItemTelevision: null,
    };
  },
  props: {
    item_sportsmen: {
      type: Object,
      default() {
        return {};
      },
    },
    idx: {
      type: Number,
      default() {
        return {};
      },
    },
    choiceNomination: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  components: {
    // ClickToEdit,
    // CodDeduction,
  },
  computed: {
    classObject: function () {
      return {
        active: this.isActive && !this.item_sportsmen.isClose,
        close: this.item_sportsmen.isClose,
      };
    },
    maxScoreExe() {
      return Math.max.apply(
        null,
        Object.values(this.item_sportsmen.executionAllResponse)
      );
    },
    minScoreExe() {
      return Math.min.apply(
        null,
        Object.values(this.item_sportsmen.executionAllResponse)
      );
    },
    maxScoreArt() {
      return Math.max.apply(
        null,
        Object.values(this.item_sportsmen.artistryAllResponse)
      );
    },
    minScoreArt() {
      return Math.min.apply(
        null,
        Object.values(this.item_sportsmen.artistryAllResponse)
      );
    },
    averageExe() {
      return +(
        (Object.values(this.item_sportsmen.executionAllResponse).reduce(
          (a, b) => a + b
        ) -
          this.minScoreExe -
          this.maxScoreExe) /
        2
      ).toFixed(3);
    },

    averageArt() {
      return +(
        (Object.values(this.item_sportsmen.artistryAllResponse).reduce(
          (a, b) => a + b
        ) -
          this.minScoreArt -
          this.maxScoreArt) /
        2
      ).toFixed(3);
    },
    //Деленая оценка сложности
    averageDif() {
      if (
        this.choiceNomination.age === "18+" &&
        (this.choiceNomination.name === "ТР" ||
          this.choiceNomination.name === "ТР 18+" ||
          this.choiceNomination.name === "ГР" ||
          this.choiceNomination.name === "ГР 18+")
      ) {
        return +(this.item_sportsmen.difficultyAllResponse.dif1 / 1.7).toFixed(
          3
        );
      } else return this.item_sportsmen.difficultyAllResponse.dif1 / 2;
    },
    //Деленая оценка сбавки за элементы
    dedElem() {
      if (this.item_sportsmen.deductionAllResponse.deductionDifficulty) {
        return this.item_sportsmen.deductionAllResponse.deductionDifficulty;
      } else {
        return 0;
      }
    },
    averageDed() {
      if (this.item_sportsmen.isClose) {
        return +(
          this.dedElem +
          this.item_sportsmen.deductionAllResponse.deductionArbitrator +
          this.item_sportsmen.deductionAllResponse.deductionLine
        );
      } else {
        return +(
          this.dedElem +
          Number(this.deduction_arb) +
          Number(this.deduction_line)
        ).toFixed(2);
      }
    },
    overallScore() {
      return +(
        this.averageExe +
        this.averageArt +
        this.averageDif -
        this.averageDed
      ).toFixed(3);
    },
  },
  methods: {
    closeSportsmen() {
      this.$emit("closeScore", {
        sportsmen: this.item_sportsmen.name,
        overallScore: this.overallScore,
      });
    },
    openSportsmen() {
      // console.log("Кликнул");
      if (!this.item_sportsmen.isClose) {
        this.isActive = !this.isActive;
        this.$store
          .dispatch(
            actionTypesSocket.socketSendSportsmen,
            this.item_sportsmen.id
          )
          .then(() => {})
          .catch((err) => {
            console.log(err);
          });
      } else return;

      // socketSendSportsmen
    },
    criterion(item) {
      console.log(item);
      this.code = item;
    },
    input(value) {
      this.newScore = value;
      console.log("ЭТО ЯЯЯЯ", this.newScore);
    },
    submitScore() {
      this.newScore = {
        id: this.item_sportsmen.id,
        averageExe: this.averageExe,
        averageArt: this.averageArt,
        averageDif: this.averageDif,
        averageDed: Number(this.averageDed),
        deductionArbitrator: Number(this.deduction_arb),
        deductionArbitratorCode: this.code,
        deductionLine: Number(this.deduction_line),
        overallScore: Number(this.overallScore),
      };
      this.$store
        .dispatch(actionTypesJudgind.postAllResult, this.newScore)
        .then((resp) => {
          console.log(resp);
          this.item_sportsmen.isClose = !this.item_sportsmen.isClose;
          this.$emit("changeData");
          this.deduction_arb = null;
          this.deduction_line = null;
          this.code = null;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    submitTelevision() {
      this.newItemTelevision = {
        id: this.item_sportsmen.id,
        fio: this.item_sportsmen.fio[0],
        nomination: this.choiceNomination.name,
        age: this.choiceNomination.age,
        averageExe: this.averageExe,
        averageArt: this.averageArt,
        averageDif: this.averageDif,
        averageDed: this.averageDed,
        overallScore: this.overallScore,
        place: this.item_sportsmen.place,
      };
      this.$store
        .dispatch(actionTypesJudgind.postTelevision, this.newItemTelevision)
        .then((resp) => {
          console.log(resp);
          this.$emit("changeData");
        })
        .catch((error) => {
          console.log(error);
        });
      // console.log("Показать на телевизоре", this.newItemTelevision);
    },
    updateScore() {
      this.$store
        .dispatch(actionTypesJudgind.openScoreArbitr, this.item_sportsmen.id)
        .then(() => {})
        .catch((error) => {
          console.log(error);
        });

      this.item_sportsmen.isClose = !this.item_sportsmen.isClose;
    },
    // putAxios() {
    //   console.log("Меняем");
    //   const forms = document.forms;
    //   const formData = new FormData(forms[1]);
    //   this.newcritical = [];
    //   for (let [name, value] of formData) {
    //     this.newcritical = { ...this.newcritical, [name]: value };
    //     console.log(this.newcritical);
    //     console.log(`${name}: ${value}`);
    //   }

    // },
  },
};
</script>

